@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Source+Sans+Pro&display=swap);
html {
  height: 100%; }

body {
  color: #333;
  margin: 0;
  height: 100%;
  overflow: auto; }

a {
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: .08rem; }

a:visited {
  color: inherit; }

