  .nav a {
    color: #333333; }

.container {
  max-width: 1200px;
  margin: auto;
  position: relative; }

.header, .footer {
  display: flex;
  flex-direction: column;
  z-index: 2; }

.header h3 {
  text-transform: uppercase;
  letter-spacing: 0.8rem;
  color: #333; }

.header h1, .header h3, .header p, .footer h2, .footer p {
  text-align: center; }

.header h2, .footer h2 {
  margin-bottom: 0; }

.header p, .footer p {
  margin-bottom: 30px; }

.footer {
  margin-top: 100px; }

.headerImage {
  max-width: 990px;
  margin: auto; }

.background {
  background-image: url(/static/media/bg.a712e828.png);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  transition: opacity 1s ease-in-out;
  width: 100%;
  z-index: -1; }

h1, h2, h3, h4, h5, h6 {
